.subscription {
  position: relative;
  min-width: $vp-1366;
  min-height: 851px;
  background: #f8f9fc;
  padding: 120px 103px;

  &::after {
    position: absolute;
    content: url("../../img/weight-plates-desktop.png");
    bottom: -157px;
    right: 0;
  }

  @include vp-1365 {
    min-width: $vp-768;
    padding: 100px 163px;
    min-height: 1779px;

    &::after {
      content: url("../../img/weight-plates-tablet.png");
      bottom: -132px;
    }
  }

  @include vp-767 {
    min-width: $vp-320;
    padding: 63px 15px;
    min-height: 1487px;

    &::after {
      content: "";
    }
  }
}

.subscription  section {
  left: 50%;
  transform: translate(-50%, 0);
  width: $vp-1366;

  @include vp-1365 {
    width: $vp-768;
  }

  @include vp-767 {
    width: $vp-320;
  }
}

.subscription  .subtitle {
  @include font-style("Alumni Sans", 600, 42px, normal, $color-text);
  letter-spacing: 1.62px;
  text-align: center;
  margin: 0 0 12px;

  @include vp-1365  {
    @include font-style("Alumni Sans", 600, 42px, normal, $color-text);
    letter-spacing: 1.26px;
    margin-bottom: 15px;
  }

  @include vp-767 {
    @include font-style("Alumni Sans", 600, 32px, normal, $color-text);
    letter-spacing: 0.96px;
    margin-bottom: 18px;
  }
}
